import {
  Button,
  Grid,
  Typography,
  Avatar,
  Stack,
  OutlinedInput,
  InputAdornment,
  IconButton,
} from "@material-ui/core";
import React, { useState, useRef } from "react";
import { goalViewStyles } from "../styles";
import SimpleBar from "simplebar-react";

import DonateButton from "../../../../../assets/icons/doantebutton.svg";
import FollowedButton from "../../../../../assets/icons/followed.png";
import FollowButton from "../../../../../assets/icons/follow.png";
import SendMessageIcon from "../../../../../assets/icons/send_message.png";
import PostViewBottomInfo from "../../../../../components/PostViewBottomInfo";
import PostViewTopInfo from "../../../../../components/PostViewTopInfo";
import { useNavigate } from "react-router";
import {
  AddCommentType,
  AddFunction,
  GoalCommentType,
  RootAppState,
  SingleGoalType,
} from "../../../../../utils/typeHelpers";
import Scrollbar from "../../../../../components/Scrollbar";
import { fDateTimeComment } from "../../../../../utils/formatTime";
import {
  ADD_COMMENT_GOAL_API,
  FOLLOW_UNFOLLOW_API,
} from "../../../../../utils/Api";
import { useSelector } from "react-redux";
import { MessageError } from "../../../../../components/AlertMessageError";

const RightColumnGoalView: React.FC<{
  data: SingleGoalType;
  handleNewComment: any;
}> = ({ data, handleNewComment }) => {
  const navigate = useNavigate();
  const userState = useSelector((state: RootAppState) => state.AuthReducer);
  const refError = useRef<null | AddFunction>(null);

  const [comment, setComment] = useState<AddCommentType>({
    goal: data.id,
    text: "",
    error: "",
  });

  const handleSendMessage = async () => {
    if (comment.text === "") {
      setComment({ ...comment, error: "Comment can't be empty!" });
      handleCreateError("Comment can't be empty!");
    } else {
      ADD_COMMENT_GOAL_API(comment, userState.userInfo.access)
        .then((e: AddCommentType) => {
          if (typeof e.goal === "number") {
            handleNewComment();
          }
        })
        .catch(() => handleCreateError("Something went wrong!"));
    }
  };

  const handleFollowUnfollow = async () => {
    if (userState.isLoggedIn && data.profile) {
      FOLLOW_UNFOLLOW_API(
        "profile-follow",
        data.profile,
        userState.userInfo.access
      ).then((res) => {
        if (res.is_followed === true) {
          handleNewComment();
        } else if (res.is_followed === false) {
          handleNewComment();
        } else {
          handleCreateError("Something went wrong");
        }
      });
    } else {
      navigate("../login");
    }
  };

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const makeDonation = () =>
    navigate("../donate", { state: { slug: data.slug } });

  return (
    <Scrollbar
      sx={{
        height: "100vh",
        "& .simplebar-content": {
          height: "100%",
          display: "flex",
          flexDirection: "column",
        },
      }}
    >
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          height: "100%",
          padding: 20,
        }}
      >
        <MessageError
          children={(add: AddFunction) => {
            refError.current = add;
          }}
        />

        <div style={{ flex: 0.5 }}>
          <Grid
            container
            direction="row"
            justifyContent="space-between"
            alignItems="center"
          >
            <PostViewTopInfo
              profileID={data.profile}
              userName={data.profile_username}
              image={data.profile_image}
              endorser={data.ngo_username}
            />

            <Button onClick={handleFollowUnfollow} aria-label="share">
              <img
                src={
                  userState.isLoggedIn
                    ? data.is_followed
                      ? FollowedButton
                      : FollowButton
                    : FollowButton
                }
                style={{ width: 80 }}
              />
            </Button>
          </Grid>
          <Grid
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center"
          >
            <div />
            <div style={{ paddingLeft: 80 }}>
              {data.goal_sdgs &&
                data.goal_sdgs?.map((goaltag) => (
                  <Typography
                    key={goaltag.title}
                    variant="caption"
                    sx={{ color: "#6C757D" }}
                  >
                    #{goaltag.title}
                  </Typography>
                ))}
            </div>
          </Grid>
          <PostViewBottomInfo
            slug={data.slug}
            goalID={data.id}
            isLiked={data?.is_liked}
            isSaved={data?.is_saved}
            title={data.title}
            total_amount={data.total_amount}
            paid_amount={data.paid_amount}
            supporters={data.goal_payment}
            total_donor={data.donor_count}
            goal_likes={data.goal_likes}
            comments={data.goal_comment}
            handleNewComment={handleNewComment}
            total_like_count={data.total_like_count}
            total_comment_count={data.total_comment_count}
          />
        </div>

        {data.status === "ACTIVE" && (
          <img
            src={DonateButton}
            alt="logo"
            style={{
              paddingLeft: 50,
              paddingRight: 50,
              paddingBottom: 20,
              cursor: "pointer",
            }}
            onClick={makeDonation}
          />
        )}
        <div style={{ flex: 0.5 }}>
          <Grid
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center"
            style={{ paddingTop: 20 }}
          >
            <div style={{ width: 20 }} />
          </Grid>
          <SimpleBar style={{ maxHeight: 400 }}>
            <Stack
              direction="column"
              justifyContent="center"
              alignItems="flex-start"
              spacing={2}
              p={3}
              sx={{ backgroundColor: "#F8F9FA" }}
            >
              {data !== undefined &&
                data.goal_comment.length > 0 &&
                data.goal_comment.map((comment) => (
                  <CommentGoal comment={comment} />
                ))}
              {userState.isLoggedIn && (
                <OutlinedInput
                  id="write_comment"
                  inputProps={{ style: { color: "black" } }}
                  fullWidth
                  value={comment.text}
                  placeholder="Write a comment..."
                  onKeyPress={(event) => {
                    if (event.key == "Enter") {
                      handleSendMessage();
                    }
                  }}
                  onChange={(e) =>
                    setComment({ ...comment, text: e.target.value, error: "" })
                  }
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="send message"
                        onClick={handleSendMessage}
                        edge="end"
                      >
                        <img
                          src={SendMessageIcon}
                          style={{ height: 25, width: 25 }}
                          alt="SendMessageIcon"
                        />
                      </IconButton>
                    </InputAdornment>
                  }
                />
              )}
              {comment.error !== "" && (
                <Typography variant="body1" sx={{ color: "red" }}>
                  {comment.error}
                </Typography>
              )}
            </Stack>
          </SimpleBar>
        </div>
      </div>
    </Scrollbar>
  );
};

export default RightColumnGoalView;
//
const CommentGoal = ({ comment }: { comment: GoalCommentType }) => {
  const classes = goalViewStyles();
  return (
    <Grid
      container
      direction="row"
      justifyContent="flex-start"
      style={{ padding: 20 }}
    >
      <Avatar
        sx={{ height: 50, width: 50 }}
        src={comment.profile_image}
        alt="Avatar1"
      />
      <div style={{ width: 20 }} />
      <Stack direction="column" justifyContent="center" alignItems="center">
        <Grid item>
          <Grid
            container
            direction="column"
            justifyContent="flex-start"
            alignItems="flex-start"
          >
            <Typography
              variant="body1"
              gutterBottom
              className={classes.commentername}
            >
              {comment.user}
            </Typography>
            <Typography
              variant="body1"
              gutterBottom
              className={classes.commenterComment}
            >
              {comment.text}
            </Typography>
            <Typography
              variant="body1"
              gutterBottom
              className={classes.commenterDate}
            >
              {fDateTimeComment(comment.created_at)}
            </Typography>
          </Grid>
        </Grid>
      </Stack>
    </Grid>
  );
};
