import React, { useState, useEffect } from "react";
import { Grid } from "@material-ui/core";
import { useLocation, useNavigate, useParams } from "react-router";
import { ErrorBoundary } from "react-error-boundary";
import { UseQueryResult, useQuery } from "react-query";
import ReactPlayer from "react-player";
import { Icon } from "@iconify/react";
import IconLeft from "@iconify/icons-ant-design/left-outline";
import IconRight from "@iconify/icons-ant-design/right-outline";
import DoneLogo from "../../../../assets/icons/doneblue.svg";
import CloseLogo from "../../../../assets/icons/close.svg";
import { goalViewStyles } from "./styles";
import RightColumnPostView from "./Components/RightColumnPostView";
import {
  RootAppState,
  SingleGoalType,
  StateRouteProps,
} from "../../../../utils/typeHelpers";

import Loader from "../../../../components/Loader";
import Scrollbar from "../../../../components/Scrollbar";
import PageErrorBoundary from "../../../Errror/PageErrorBoundary";
import { useSelector } from "react-redux";
import ApiErrorPage from "../../../Errror/ApiErrorPage";
import { useWindowDimensions } from "../../../../utils/helperFunction";
import MetaTags from "react-meta-tags";
import ErrorCatchComponent from "../../../../components/ErrorCatchComponent";
import useAxios from "../../../../utils/useAxios";

const GoalView = () => {
  const classes = goalViewStyles();
  const navigate = useNavigate();
  const location = useLocation();
  const { slug } = useParams();
  // console.log(location);
  const userState = useSelector((state) => state.AuthReducer);
  const videoIndex = location.state ? location.state.index : 0;

  const [explode, setExplode] = useState(false);

  let [activeIndex, setActiveIndex] = useState(videoIndex ? videoIndex : 0);
  const [currentVideo, setCurrentVideo] = useState("");
  const [newComment, setNewCommet] = useState(false);

  const handleNewComment = () => setNewCommet(!newComment);

  const axiosInstance = useAxios();
  const querySingleGoal = useQuery(
    [
      "getSingleGoal",
      [slug, userState.isLoggedIn ? userState.userInfo.access : "", newComment],
    ],
    async () => {
      const response = await axiosInstance.get(`/retrieve-goal/${slug}`);

      return response.data;
    }
  );

  const { data, isError, isLoading, error } = querySingleGoal;
  useEffect(() => {
    if (slug === "" && data !== null) {
      navigate("../");
    } else if (data?.detail === "Not found.") {
      navigate("../");
    }
  }, [slug, data]);

  useEffect(() => {
    if (data !== undefined && data !== null && data.goal_media) {
      if (data.goal_media.length > 0) {
        setCurrentVideo(
          activeIndex
            ? data?.goal_media[activeIndex].file
            : data?.goal_media[0].file
        );
      }
    }
  }, [data]);
  const { height, width } = useWindowDimensions();

  // console.log('$$$ view goal comming : ');

  return (
    <div>
      {/* <MetaTags>
        <meta property="og:description" content={data?.short_description} />
        <meta property="og:title" content={data?.title} />
        <meta property="og:image" content={data?.image} />
      </MetaTags> */}
      <ErrorCatchComponent>
        {isLoading ? (
          <Loader />
        ) : (
          <div>
            {isError ? (
              <ApiErrorPage error={new Error("Network Error")} />
            ) : (
              data !== undefined &&
              data !== null && (
                <ErrorBoundary
                  fallbackRender={({ error, resetErrorBoundary }) => {
                    return (
                      <PageErrorBoundary
                        error={error}
                        resetErrorBoundary={resetErrorBoundary}
                      />
                    );
                  }}
                  onReset={() => setExplode(false)}
                  resetKeys={[explode]}
                >
                  <Grid container direction="row">
                    <Grid item xs={12} sm={8}>
                      <Scrollbar
                        sx={{
                          height: "100vh",
                          "& .simplebar-content": {
                            height: "100%",
                            display: "flex",
                            flexDirection: "column",
                          },
                        }}
                      >
                        <img
                          onClick={() => navigate("../")}
                          className={classes.doneLogo}
                          src={DoneLogo}
                          alt="Paella dish"
                        />

                        <img
                          onClick={() => navigate(`../../`)}
                          className={classes.closeLogo}
                          src={CloseLogo}
                          alt="Paella dish"
                        />
                        <Grid
                          container
                          justifyContent="center"
                          alignItems="center"
                          alignContent="center"
                          className={classes.imageContainer}
                          sx={{ pt: 10 }}
                        >
                          <Grid
                            container
                            direction="row"
                            justifyContent="center"
                            alignItems="center"
                            spacing={2}
                          >
                            <Grid item md={1}>
                              {data !== undefined && activeIndex >= 1 && (
                                <Icon
                                  icon={IconLeft}
                                  color="white"
                                  height={40}
                                  width={40}
                                  onClick={() => {
                                    if (data !== undefined) {
                                      let index = activeIndex - 1;
                                      setActiveIndex(index);
                                      setCurrentVideo(
                                        data?.goal_media[index].file
                                      );
                                    }
                                  }}
                                />
                              )}
                            </Grid>
                            <Grid
                              item
                              md={10}
                              style={{ maxHeight: height - 100 }}
                            >
                              <ReactPlayer
                                className="react-player"
                                width="100%"
                                height={height - 100}
                                url={currentVideo}
                                loop={true}
                                playing={true}
                                controls={true}
                                config={{
                                  file: {
                                    attributes: {
                                      controlsList: "nodownload noplaybackrate",
                                      disablePictureInPicture: "true",
                                    },
                                  },
                                }}
                              />
                            </Grid>
                            <Grid item md={1}>
                              {data !== undefined &&
                                data.goal_media.length > 1 &&
                                activeIndex < data.goal_media.length - 1 && (
                                  <Icon
                                    icon={IconRight}
                                    color="white"
                                    height={40}
                                    width={40}
                                    onClick={() => {
                                      if (data !== undefined) {
                                        if (
                                          activeIndex <
                                          data.goal_media.length - 1
                                        ) {
                                          let index = activeIndex + 1;
                                          setActiveIndex(index);
                                          setCurrentVideo(
                                            data?.goal_media[index].file
                                          );
                                        }
                                      }
                                    }}
                                  />
                                )}
                            </Grid>
                          </Grid>
                        </Grid>
                      </Scrollbar>
                    </Grid>

                    <Grid item xs={12} sm={4}>
                      {data !== undefined && (
                        <RightColumnPostView
                          data={data}
                          handleNewComment={handleNewComment}
                        />
                      )}
                    </Grid>
                  </Grid>
                </ErrorBoundary>
              )
            )}
          </div>
        )}
      </ErrorCatchComponent>
    </div>
  );
};

export default GoalView;
