import { Avatar, Button, Grid, Typography } from "@material-ui/core";
import React, { useRef, useState } from "react";

import ShareIcon from "../assets/icons/share_blue.svg";
import CommentIcon from "../assets/icons/comment.svg";

import FavoriteIconOutline from "../assets/icons//heart.svg";
import FavoriteIconfill from "../assets/icons//heartfilled.svg";
import SavedIconOutline from "../assets/icons/pin_outline.svg";
import SavedIconFill from "../assets/icons//pin.svg";
import CustomLinearProgress from "./CustomLinearProgress";

import { goalViewStyles } from "../screen/Home/Common/GoalView/styles";
import {
  AddFunction,
  GoalCommentType,
  GoalPayemtType,
  RootAppState,
} from "../utils/typeHelpers";
import { useSelector } from "react-redux";
import {
  APP_URL,
  GOAL_LIKE_UNLIKE_API,
  GOAL_SAVE_UNSAVE_API,
} from "../utils/Api";
import { useNavigate } from "react-router";
import { MessageError } from "./AlertMessageError";
import ShareMenu from "./ShareMenu";

const PostViewBottomInfo = ({
  goalID,
  isLiked,
  isSaved,
  title,
  total_amount,
  paid_amount,
  supporters,
  total_donor,
  goal_likes,
  comments,
  handleNewComment,
  total_like_count,
  total_comment_count,
  slug,
}: {
  slug?: string;
  goalID?: number;
  isLiked?: boolean;
  isSaved?: boolean;
  title?: string;
  total_amount?: string;
  paid_amount?: string;
  supporters?: GoalPayemtType[];
  total_donor?: string;
  goal_likes?: number;
  comments?: GoalCommentType[];
  handleNewComment: any;
  total_like_count?: number;
  total_comment_count?: number;
}) => {
  // console.log();

  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  const navigate = useNavigate();
  const classes = goalViewStyles();
  const percentage =
    (100 * parseInt(paid_amount ? paid_amount : "0")) /
    parseInt(total_amount ? total_amount : "0");

  const refError = useRef<null | AddFunction>(null);

  const handleLikeUnlike = async () => {
    if (userState.isLoggedIn && goalID) {
      GOAL_LIKE_UNLIKE_API(goalID, userState.userInfo.access).then((res) => {
        if (res.is_like === true) {
          handleNewComment();
        } else if (res.is_like === false) {
          handleNewComment();
        } else {
          handleCreateError("Something went wrong");
        }
      });
    } else {
      navigate("../login");
    }
  };

  const handleSaveUnSave = async () => {
    if (userState.isLoggedIn && goalID) {
      GOAL_SAVE_UNSAVE_API(goalID, userState.userInfo.access).then((res) => {
        if (res.is_saved === true) {
          handleNewComment();
        } else if (res.is_saved === false) {
          handleNewComment();
        } else {
          handleCreateError("Something went wrong");
        }
      });
    } else {
      navigate("../login");
    }
  };

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleSeeOtherDonorProfile = (e: number) => {
    if (e !== null) {
      navigate("../profiledonorother", {
        state: { goalID: e },
      });
    }
  };

  return (
    <Grid
      container
      direction="column"
      justifyContent="flex-start"
      style={{ padding: 40 }}
    >
      <MessageError
        children={(add: AddFunction) => {
          refError.current = add;
        }}
      />

      <Typography variant="body1" gutterBottom className={classes.infoTitle}>
        {title}
      </Typography>
      <Typography
        variant="body1"
        gutterBottom
        className={classes.moneyGot}
        align="left"
      >
        US ${Number(paid_amount).toLocaleString()}{" "}
        <span className={classes.moneyGotSub}>
          {" "}
          of US ${Number(total_amount).toLocaleString()}
        </span>
      </Typography>
      <CustomLinearProgress percentage={percentage} />
      <div style={{ height: 20 }} />
      <Grid
        container
        direction="row"
        justifyContent="flex-start"
        alignItems="center"
      >
        {supporters !== undefined &&
        supporters?.length >= 1 &&
        supporters?.length >= 5
          ? supporters?.slice(0, 6).map((supporter: GoalPayemtType) => (
              <Avatar
                onClick={() => handleSeeOtherDonorProfile(supporter.user.id)}
                src={supporter.user.image}
                sx={{
                  height: 50,
                  width: 50,
                  marginRight: 1,
                  cursor: "pointer",
                }}
              />
            ))
          : supporters?.map((supporter: GoalPayemtType) => (
              <Avatar
                onClick={() => handleSeeOtherDonorProfile(supporter.user.id)}
                src={supporter.user.image}
                sx={{
                  height: 50,
                  width: 50,
                  marginRight: 1,
                  cursor: "pointer",
                }}
              />
            ))}
      </Grid>
      {supporters !== undefined && supporters?.length >= 1 && total_donor && (
        <Typography
          variant="body1"
          pt={1}
          pb={1}
          style={{ cursor: "pointer" }}
          className={classes.titleEndorseBy}
          onClick={() => handleSeeOtherDonorProfile(supporters[0].user.id)}
        >
          @{supporters[0].user.username}{" "}
          <span style={{ color: "#000000", cursor: "default" }}>
            {" "}
            {total_donor !== "0" ? `and ${total_donor} more people` : ""} donate
            to this goal{" "}
          </span>
        </Typography>
      )}

      <Grid
        container
        direction="row"
        justifyContent="space-between"
        alignItems="center"
      >
        <Button
          onClick={handleLikeUnlike}
          className={classes.buttonIcon}
          startIcon={
            <img
              src={
                userState.isLoggedIn
                  ? isLiked
                    ? FavoriteIconfill
                    : FavoriteIconOutline
                  : FavoriteIconOutline
              }
              alt="logo"
              className="cardIcons"
            />
          }
        >
          {total_like_count}
        </Button>
        <Button
          className={classes.buttonIcon}
          startIcon={<img src={CommentIcon} alt="logo" className="cardIcons" />}
        >
          {total_comment_count}
        </Button>
        <Button
          onClick={handleSaveUnSave}
          className={classes.buttonIcon}
          startIcon={
            <img
              src={
                userState.isLoggedIn
                  ? isSaved
                    ? SavedIconFill
                    : SavedIconOutline
                  : SavedIconOutline
              }
              style={{ height: 30, width: 30 }}
              alt="logo"
              className="cardIcons"
            />
          }
        />
        <ShareMenu
          url={`${APP_URL}/viewgoal/${slug}`}
          image="https://donee.s3.amazonaws.com/media/images/goal_images/photo-1488521787991-ed7bbaae773c_e4nGOZ5.jpeg"
        />
      </Grid>
    </Grid>
  );
};

export default PostViewBottomInfo;
