import React from "react";
import { Grid, Typography } from "@material-ui/core";

import { goalViewStyles } from "../screen/Home/Common/GoalView/styles";
import { useNavigate } from "react-router";

interface PostViewTopInfoProps {
  profileID?: number;
  userName?: string;
  image?: string;
  endorser?: string;
}

const PostViewTopInfo = ({
  userName,
  image,
  endorser,
  profileID,
}: PostViewTopInfoProps) => {
  const classes = goalViewStyles();
  const navigate = useNavigate();
  const handleSeeOtherProfile = () =>
    navigate("../../profiledetailother", {
      state: { goalID: profileID },
    });
  return (
    <div>
      <Grid
        container
        direction="row"
        justifyContent="flex-start"
        alignItems="center"
      >
        <div>
          <img className={classes.avatar} src={image} alt="Paella dish" />
        </div>
        <div style={{ width: 20 }} />
        <Grid>
          <div onClick={handleSeeOtherProfile}>
            <Typography
              style={{ cursor: "pointer" }}
              variant="body1"
              gutterBottom
              className={classes.title}
            >
              @{userName}
            </Typography>
          </div>

          <Typography
            variant="body1"
            gutterBottom
            className={classes.titleEndorse}
          >
            Endorsed By{" "}
            <span
              style={{ cursor: "pointer" }}
              className={classes.titleEndorseBy}
            >
              @{endorser}
            </span>
          </Typography>
        </Grid>
      </Grid>
    </div>
  );
};

export default PostViewTopInfo;
